.text-widget-container {
  width: 100%;
  height: 100%;
  min-height: 50px;
  position: relative;

  .edit-mode {
    display: flex;
    flex-direction: column;
    gap: 8px;
    height: 100%;

    .text-editor {
      width: 100%;
      flex: 1;
      padding: 12px;
      border: 2px solid #2196F3;
      border-radius: 4px;
      outline: none;
      font-family: inherit;
      resize: vertical;
      min-height: 100px;
      background: white;
      
      &:focus {
        border-color: #1976D2;
        box-shadow: 0 0 0 3px rgba(33, 150, 243, 0.1);
      }
    }

    .edit-toolbar {
      display: flex;
      align-items: center;
      gap: 4px;
      padding: 8px;
      background: #f5f5f5;
      border-radius: 4px;
      flex-wrap: wrap;

      .spacer {
        flex: 1;
      }

      button {
        &:not([mat-raised-button]) {
          opacity: 0.7;
          transition: opacity 0.2s;

          &:hover {
            opacity: 1;
          }
        }
      }
    }
  }

  .text-display {
    width: 100%;
    height: 100%;
    min-height: 50px;
    padding: 12px;
    cursor: pointer;
    transition: all 0.2s;
    border: 2px dashed transparent;
    border-radius: 4px;
    word-wrap: break-word;
    white-space: pre-wrap;

    &:hover {
      border-color: #e0e0e0;
      background: rgba(0, 0, 0, 0.02);
    }

    &:active {
      background: rgba(0, 0, 0, 0.04);
    }
  }
}

// 편집 중일 때 전체 컨테이너 스타일
:host {
  display: block;
  width: 100%;
  height: 100%;
}
